<template>
  <div>
  <h3>添加学生成绩</h3>
  <el-form  :model="form" ref="form" label-width="80px">
  <el-form-item label="学号" style="width: 300px;" >
    <el-input v-model="form.id"></el-input>
  </el-form-item>
  <el-form-item label="选择课程">
    <el-select v-model="form.region" placeholder="请选择添加的课程">
      <el-option label="高代" value="2"></el-option>
      <el-option label="数分" value="1"></el-option>
      <el-option label="解析几何" value="3"></el-option>
      <el-option label="大学英语" value="4"></el-option>
      <el-option label="大学物理" value="5"></el-option>
      <el-option label="计算机基础" value="6"></el-option>
      <el-option label="大学体育" value="7"></el-option>
    </el-select>
  </el-form-item>
  </el-form-item>
  <el-form-item label="平时成绩"style="width: 300px;" >
    <el-input v-model="form.pingshi"></el-input>
  </el-form-item>
  <el-form-item label="期中成绩" style="width: 300px;">
    <el-input v-model="form.qizhong"></el-input>
  </el-form-item>
  <el-form-item label="实习成绩"style="width: 300px;" >
    <el-input v-model="form.shixi"></el-input>
  </el-form-item>
  <el-form-item label="期末成绩" style="width: 300px;">
    <el-input v-model="form.qimo"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit">立即添加</el-button>
    <el-button>取消</el-button>
  </el-form-item>
  </el-form>
  </div>
</template>

<script>
  export default {
    name: 'addGrade',
    data () {
      return {
        form: {
          id: '',
          region: '',
          pingshi: '',
          qizhong: '' ,
          shixi: '',
          qimo: ''
          }
      }
    },
    methods:{
      onSubmit() {
        const _this = this ;
        //先计算总评成绩
        var zongping = 0 ;
        zongping = (Number(_this.form.pingshi) + Number(_this.form.qizhong) + Number(_this.form.shixi) + Number(_this.form.qimo))/4;
        //发送异步请求添加成绩
        _this.$http.post("http://localhost:8989/grade/add",{
          sid: _this.form.id ,
          cid: _this.form.region ,
          pingshi: _this.form.pingshi ,
          qizhong: _this.form.qizhong ,
          shixi: _this.form.shixi ,
          qimo: _this.form.qimo ,
          zongping: zongping ,
        }).then(function(response) {
          if(response.data == 1) {
            _this.form = '' ;
            _this.$alert('添加成功', '标题名称', {
                      confirmButtonText: '确定',
                      callback: action => {
                        this.$message({
                          type: 'info',
                          message: `action: ${ action }`
                        });
                      }
                    });
          }else{
            _this.form = '' ;
            _this.$alert('添加失败', '标题名称', {
                      confirmButtonText: '确定',
                      callback: action => {
                        this.$message({
                          type: 'info',
                          message: `action: ${ action }`
                        });
                      }
        });
      }
      })
      }
     },
   }
</script>

<style>
</style>



