<template>
  <div>
    <h3>添加学生</h3>
<el-form  :model="form" ref="form" label-width="80px">
  <el-form-item label="学号" style="width: 300px;" >
    <el-input v-model="form.id"></el-input>
  </el-form-item>
  <el-form-item label="姓名" style="width: 300px;">
    <el-input v-model="form.name"></el-input>
  </el-form-item>
  <el-form-item label="年龄"style="width: 300px;" >
    <el-input v-model="form.age"></el-input>
  </el-form-item>
  <el-form-item label="住址" style="width: 300px;">
    <el-input v-model="form.address"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit1">立即添加</el-button>
    <el-button>取消</el-button>
  </el-form-item>
</el-form>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      form: {
        id: '',
        name: '',
        age: '',
        address: '' ,
        }
    }
  },
methods: {
      onSubmit1() {
        const _this = this ;
        this.$http.post("http://localhost:8989/student/add",{
          id: _this.form.id ,
          name: _this.form.name ,
          age: _this.form.age ,
          address: _this.form.address
        }).then(function(response){
          if(response.data == 1) {
            _this.form = '' ;
            _this.$alert('添加成功', '标题名称', {
                      confirmButtonText: '确定',
                      callback: action => {
                        this.$message({
                          type: 'info',
                          message: `action: ${ action }`
                        });
                      }
                    });
          }else{
            _this.form = '' ;
            _this.$alert('添加失败', '标题名称', {
                      confirmButtonText: '确定',
                      callback: action => {
                        this.$message({
                          type: 'info',
                          message: `action: ${ action }`
                        });
                      }
                    });
          }
        })
      }
},

}
</script>



