<template>
  <div id="app">
    <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
      <el-menu-item index="1">添加学生</el-menu-item>
      <el-menu-item index="5">添加学生成绩</el-menu-item>
      <el-menu-item index="2">展示所有学生的基本信息</el-menu-item>
      <el-menu-item index="3">查看学生的个人成绩</el-menu-item>
      <el-menu-item index="4">查看课程的所有学生成绩</el-menu-item>
    </el-menu>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
        return {
          activeIndex: '1',
          activeIndex2: '1'
        };
      },
      methods: {
        handleSelect(key) {
          //key的数据为 1 2 3 4 表示第几个菜单项被选中
          if(key == 1) {
            this.$router.push({path:'/HelloWorld'})
          }else if(key == 2) {
            this.$router.push({path:'/showall'})
          }else if (key == 3) {
            this.$router.push({path:'/showGrade'})
          }else if (key == 4) {
            this.$router.push({path:'/showCourse'})
          }else if (key == 5) {
            this.$router.push({path:'/addGrade'})
          }
        }
      }
}
</script>



