<template>
  <div>
  <h3>展示所有的学生信息</h3>
<el-table
      :data="tableData"
      style="width: 100%">
      </el-table-column>
      <el-table-column
        prop="id"
        label="学生编号"
        width="180">
      </el-table-column>
      <el-table-column
        prop="name"
        label="学生姓名"
        width="180">
      </el-table-column>
      <el-table-column
        prop="age"
        label="学生年龄"
        width="180">
      </el-table-column>
      <el-table-column
        prop="address"
        label="家庭地址"
        width="300">
      </el-table-column>
    </el-table>
    </div>
</template>

<script>
   export default {
        data() {
          return {
            tableData: [{
              id: '' ,
              name: '',
              age: '' ,
              address: '' ,
            }, {
            id: '' ,
            name: '',
            age: '' ,
            address: '' ,
            }, {
           id: '' ,
           name: '',
           age: '' ,
           address: '' ,
            }, {
             id: '' ,
             name: '',
             age: '' ,
             address: '' ,
            }]
          }
        },
        mounted() {
          //发送axios请求后端数据
          const _this = this ;
          this.$http.get("http://localhost:8989/student/findAll").then(function (response) {
            _this.tableData = response.data ;
          });
        }
      }
</script>

<style>
</style>



