<template>
  <div>
    <h3>查看课程的所有学生成绩</h3>
    <!-- 选择课程 -->
    <el-select v-model="id" placeholder="请选择课程">
        <el-option
          v-for="item in options"
          :key="item.id"
          :label="item.name"
          :value="item.id">
        </el-option>
      </el-select>
      <el-button type="primary" @click="onSubmit2">确认</el-button>
      <br/><br/>
      <!-- 升降序排列 -->
      <el-row>
        <el-button type="warning" @click='shengxu()' v-show = showall>从高到低</el-button>
        <el-button type="danger" @click='jiangxu()' v-show = showall>从低到高</el-button>
      </el-row>
      <br/>
      <!-- 列出选中的课程的每个学生的成绩 -->
       <el-table
       v-show=showall
            :data="tableData"
            style="width: 100%">
            <el-table-column
              prop="name"
              label="科目"
              width="180">
            </el-table-column>
            <el-table-column
              prop="pingshi"
              label="平时成绩">
            </el-table-column>
            <el-table-column
              prop="qizhong"
              label="期中成绩">
            </el-table-column>
            <el-table-column
              prop="shixi"
              label="实习成绩">
            </el-table-column>
            <el-table-column
              prop="qimo"
              label="期末成绩">
            </el-table-column>
            <el-table-column
              prop="zongping"
              label="总评成绩">
            </el-table-column>
          </el-table>

          <div v-show = showall>
            <el-tag> 所有学生的总评分为</el-tag><el-tag type="danger">{{ans}}</el-tag>
            <el-tag> 所有学生的平均总评分为</el-tag><el-tag type="danger">{{pingjun}}</el-tag>
          </div>

          <!-- 选择分数段 -->
          <br />
          <el-select v-model="idd" placeholder="请选择分数段" v-show=showall>
              <el-option
                v-for="item in optionss"
                :key="item.idd"
                :label="item.name"
                :value="item.idd">
              </el-option>
            </el-select>
            <el-button type="primary" @click="onSubmit3" v-show=showall>确认</el-button>
            <div v-show="show">
              <el-tag>该分数段的人数</el-tag>
              <el-tag type="danger">{{tableData.length}}</el-tag>
              <el-tag>人</el-tag>
            </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
    id: '' ,//课程id
    idd: '' ,//分数段id
    number: '',//分数段人数
    value: true ,
    showall: false ,
    show: false ,
    ans: 0 ,
    pingjun: 0 ,
    options: [
      {
        id: '1' ,
        name: '数分'
      },
      {
        id: '2' ,
        name: '高代'
      },
      {
        id: '3' ,
        name: '解析几何'
      },
      {
        id: '4' ,
        name: '大学英语'
      },
      {
        id: '5' ,
        name: '大学物理'
      },
      {
        id: '6' ,
        name: '计算机基础'
      },
      {
        id: '7' ,
        name: '大学体育'
      }
  {
        id: '8' ,
        name: '数据库实训'
      }  ],
    optionss: [
      {
        idd: '1' ,
        name: '60分以下'
      },
      {
        idd: '2' ,
        name: '60-70分之间'
      },
      {
        idd: '3' ,
        name: '70-80分之间'
      },
      {
        idd: '4' ,
        name: '80-90分之间'
      },
      {
        idd: '5' ,
        name: '90-100分之间'
      }
    ],
    tableData: [{
               name: '',
               pingshi: '',
               qizhong: '',
               shixi: '' ,
               qimo: '',
               zongping: '' ,
             }],
  }
  },
  methods:{
    onSubmit2() {
      //获取的是课程id
      console.log(this.id);
      this.showall = true ;
      //根据课程id获取该课程所有学生的成绩
      const _this = this ;
      _this.$http.get("http://localhost:8989/course/getGradeByCourseId?id=" + _this.id).then(function(response) {
        _this.tableData = response.data ;
       var length =  response.data.length;
       var ans = 0 ;
       for(var i=0; i<length ; i++) {
         ans = ans + Number(response.data[i].zongping);
       }
       _this.pingjun = ans/length ;
       _this.ans = ans ;

      })
    },
    onSubmit3() {
      //获取的是分数段idd
      console.log(this.idd);
      //根据分数段idd获取该分数段的学生信息
      const _this = this ;
      _this.$http.get("http://localhost:8989/course/getGradeByCourseByFenShuDuan?id=" + _this.id + "&idd=" + _this.idd).then(function(response) {
        _this.tableData = response.data ;
        var length =  response.data.length;
        var ans = 0 ;
        for(var i=0; i<length ; i++) {
          ans = ans + Number(response.data[i].zongping);
        }
        _this.ans = ans ;
        _this.pingjun = ans/length ;
      })
      this.show = true ;
    },
    shengxu() {
      this.tableData.sort(function (x,y) {
            return y.zongping-x.zongping;
        });
    },
    jiangxu() {
      this.tableData.sort(function (x,y) {
            return x.zongping-y.zongping;
        });
    }
  }
}
</script>

<style>
</style>



