<template>
  <div>
    <h3>列出某个学生的所有成绩</h3>
    <el-select v-model="id" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.id"
          :label="item.name"
          :value="item.id">
        </el-option>
      </el-select>
      <el-button type="primary" @click="onSubmit2">确认</el-button>
	  <!-- 升降序排列 -->
	  <el-row>
	    <el-button type="warning" @click='shengxu()' v-show= showall>从高到低</el-button>
	    <el-button type="danger" @click='jiangxu()' v-show = showall>从低到高</el-button>
	  </el-row>
      <!-- 列出选中的学生的每个科目的成绩 -->
       <el-table
       v-show= show
            :data="tableData"
            style="width: 100%">
            <el-table-column
              prop="name"
              label="科目"
              width="180">
            </el-table-column>
            <el-table-column
              prop="pingshi"
              label="平时成绩">
            </el-table-column>
            <el-table-column
              prop="qizhong"
              label="期中成绩">
            </el-table-column>
            <el-table-column
              prop="shixi"
              label="实习成绩">
            </el-table-column>
            <el-table-column
              prop="qimo"
              label="期末成绩">
            </el-table-column>
            <el-table-column
              prop="zongping"
              label="总评成绩">
            </el-table-column>
          </el-table>    
  </div>
</template>

<script>
  export default {
  data () {
    return {
      showall: false ,
      options: [{
                id: '',
                name: ''
              }, {
                id: '',
                name: ''
              }, {
                id: '',
                name: ''
              }, {
                id: '',
                name: ''
              }, {
                id: '',
                name: ''
              }],
      tableData: [{
                 name: '',
                 pingshi: '',
                 qizhong: '',
                 shixi: '' ,
                 qimo: '',
                 zongping: '' ,
               }],
              id: '' ,
              show: false
    }
  },
  methods:{
    onSubmit2() {
      //id为选中的学生id
      console.log(this.id);
      this.show = true ;
      this.showall = true ;
      //根据id查询对应的tabledata
      const _this = this ;
      _this.$http.get("http://localhost:8989/student/findCourse?id=" + _this.id).then(function(response) {
        _this.tableData = response.data ;
      })
    },
    shengxu() {
      this.tableData.sort(function (x,y) {
            return y.zongping-x.zongping;
        });
    },
    jiangxu() {
      this.tableData.sort(function (x,y) {
            return x.zongping-y.zongping;
        });
    }
  },
  mounted() {
    const _this = this ;
    _this.$http.get("http://localhost:8989/student/findAll").then(function(response) {
      _this.options = response.data ;
    })
  },
  }
</script>

<style>
</style>



